<template>
  <div id="login">
    <div class="login-wrapper" v-if="!register">
      <div class="header">登录</div>
      <div class="form-wrapper">
        <input type="text" name="username" placeholder="邮箱" class="input-item" v-model="username">
        <input type="password" name="password" placeholder="密码" class="input-item" v-model="password">
        <div class="btn" @click="login()">登录</div>
      </div>
      <div class="msg">
        没有账号？
        <a class="sign-up" href="#" @click="turnToRegister()">注册</a>
      </div>
    </div>
    
    <div class="login-wrapper" v-if="register">
      <div class="header">注册</div>
      <div class="form-wrapper">
        <input type="text" name="username" placeholder="邮箱" class="input-item" v-model="username">
        <input type="password" name="password" placeholder="密码" class="input-item" v-model="password">
        <input type="number" name="code" placeholder="验证码" class="input-item" v-model="code">
        <el-button style="width: 120px;" @click="getCode()" :disabled="this.disabled">{{ buttonText }}</el-button>
        <div class="btn" @click="signUp()">注册</div>
      </div>
      <div class="msg">
        已有账号？
        <a class="sign-up" href="#" @click="turnToLogin()">登录</a>
      </div>
    </div>
  </div>
</template>

<script>
import { setCookie, getCookie, delCookie } from '@/network/cookie.js'
function stringify (data) {
  const formData = new FormData()
  for (const key in data) {
    // eslint-disable-next-line no-prototype-builtins
    if (data.hasOwnProperty(key)) {
      if (data[key]) {
        if (data[key].constructor === Array) {
          if (data[key][0]) {
            if (data[key][0].constructor === Object) {
              formData.append(key, JSON.stringify(data[key]))
            } else {
              data[key].forEach((item, index) => {
                formData.append(key + `[${index}]`, item)
              })
            }
          } else {
            formData.append(key + '[]', '')
          }
        } else if (data[key].constructor === Object) {
          formData.append(key, JSON.stringify(data[key]))
        } else {
          formData.append(key, data[key])
        }
      } else {
        if (data[key] === 0) {
          formData.append(key, 0)
        } else {
          formData.append(key, '')
        }
      }
    }
  }
  return formData
}
export default {
  name: 'Login',
  data() {
    return {
      register: false,
      username: '',
      password: '',
      code: '',
      disabled: false,
      countdown: 60
    }
  },
  computed: {
    buttonText() {
      if (this.countdown == 60) {
        return '获取验证码'
      }
      else {
        return this.countdown + 's后获取'
      }
    }
  },
  methods: {
    login() {
      let that = this
      
        // setCookie('username', this.username, 1000*60)
        //     this.$router.replace('/home')
      let dd = stringify({
        username: this.username,
        password: this.password,
        rememberMe: true,
      })
      this.$.ajax({
        type: "post",
        url: 'http://localhost:8098/api/auth/login',
        async: true,
        data: {
          username: this.username,
          password: this.password,
          rememberMe: true,
        },
        dataType: 'json',
        xhrFields: {
            withCredentials: true
        },
        success: function(res) {
          if (res.code == 403) {  // 登陆失败
            that.$message({
              message: res.reason,
              type: 'error',
              duration: 2000
            })
          }
          else {          
            that.$request({
              url: '/api/user/info'
            }).then(res => {
              that.$store.commit("setUserInfo", res.data)
              window.localStorage.setItem('userInfo', JSON.stringify(res.data))
              setCookie('username', that.username, 1000*60)
              that.$router.replace('/home')
            })
          }

        }
      });
    },
    signUp() {
      let ddd = stringify({
        email: this.username,
        password: this.password,
        verify: this.code,
      })
      let that = this
      let xmlHttp = new XMLHttpRequest();
      xmlHttp.open('POST', 'http://localhost:8098/api/auth/register', true);
      xmlHttp.onreadystatechange = function() {
        if (xmlHttp.readyState === 4 && xmlHttp.status === 200) {
          let response = JSON.parse(xmlHttp.response);
          if (response.code == 500) {
            that.$message({
              message: '请输入正确的邮箱',
              type: 'error',
              duration: 2000
            })
          }
          else if (response.code == 403) {
            that.$message({
              message: '请输入正确的验证码',
              type: 'error',
              duration: 2000
            })
          }
          else {
            that.$message({
              message: '注册成功, 正在跳转回登录页面',
              type: 'success',
              duration: 2000
            })
            setTimeout(() => {
              that.register = false
            })
          }
        }
        else if (xmlHttp.readyState === 4 && xmlHttp.status !== 200){
          alert("网络错误，请稍后重试");
        }
      }
      xmlHttp.send(ddd);

    },
    getCode() {
      // 发送验证码
      this.$request({
        url: '/api/auth/verify-code?email=' + this.username
      }).then(res => {
        if (res.code == 500) {
          this.$message({
            message: '请输入正确的邮箱',
            type: 'error',
            duration: 2000
          })
        }
      })

      // 按钮改变
      this.disabled = true
      let timer = setInterval(() => {
        this.countdown--
        if (this.countdown == 0) {
          this.disabled = false
          this.countdown = 60
          clearInterval(timer)
        }
      }, 1000)
    },
    turnToRegister() {
      this.register = true
    },
    turnToLogin() {
      this.register = false
    },
  },
  created() {
    // 如果已经登录, 跳转到home页
    // this.$router.replace('/home')
  }
}
</script>

<style>
  #login {
    height: 100%;
    width: 100%;
    background-image: linear-gradient(to right, #F5F5F5, #F5F5F5);
    position: fixed;
  }
  .login-wrapper {
    box-shadow: 0 0 8px #e9ecf0;
    background-color: #fff;
    width: 358px;
    height: 588px;
    border-radius: 15px;
    padding: 0 50px;
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  .form-wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: end;
  }
  .header {
    font-size: 38px;
    font-family:'Courier New', Courier, monospace;
    font-weight: bolder;
    text-align: center;
    line-height: 200px;
  }
  .input-item {
    display: block;
    width: 100%;
    margin-bottom: 20px;
    border: 0;
    padding: 10px;
    border-bottom: 1px solid rgb(128, 125, 125);
    font-size: 15px;
    outline: none;
    box-sizing: border-box;
  }
  .input-item:placeholder {
    text-transform: uppercase;
  }
  .btn {
    text-align: center;
    padding: 10px;
    width: 100%;
    margin-top: 40px;
    background-image: linear-gradient(to right,  #edeaec,#a6c1ee);
    color: #fff;
    cursor: pointer;
    border-radius: 5px;
    box-sizing: border-box;
  }
  .btn:hover {
    background-image: linear-gradient(to right, #a6c1ee, #edeaec);
  }

  .msg {
    text-align: center;
    line-height: 88px;
  }
  .sign-up {
    text-decoration-line: none;
    color: #abc1ee;
  }
</style>